import React from 'react'
import Helmet from 'react-helmet'
import AppIcons from '../partials/AppIcons'
import FooterInfo from '../partials/FooterInfo'

const DownloadPage = () => (
  <div>
    <Helmet
      title="Download the Fitato App"
      meta={[
        { name: 'description', content: 'Fitato app is available to download on Google Play Store and App Store for iOS.' },
      ]}
    />
    <div className="download-page align-center">
      <div className="container">
        <h1>Download the Fitato App</h1>
        <AppIcons />
      </div>
    </div>
    <FooterInfo />
  </div>
)

export default DownloadPage



